body {
  margin: 0;
  font-family: "Lato";
  text-decoration: none;
}

.cointenerMenu{
    background: #0D0D0D;
    width: 20%;
    text-align: center;
    padding: 100px 20px 20px 20px;
    height: auto;
 /*   box-shadow: 5px 0px black;  */
}
.logoMenu{
    width: 40%;
   margin: auto;
}
.cointenerButtons{

    text-decoration: none;

}
.buttonMenu{
    color: #B3B3B3;
    margin:50px 0 0 80px;
    font-family: "Lato";
    font-size: 18px;
    text-align: left;
    width: 50%;
    cursor: pointer;
}

.buttonMenu:hover{
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(70%, transparent), color-stop(0%, #00FA77));
    background-image: linear-gradient(transparent 70%, #00FA77 0%);
    background-position: left 30%;
    -webkit-transition: all 0.75s linear;
    transition: all 0.75s linear;
}
.cointenerMain {
    background-color: #171617;
    display: flex;
    height: 100vh;
}
.titleMain{
    width: 80%;
    color: cornsilk;
    padding: 40px;
    font-size: 20px;
    font-family: "Lato";
}
.cointenerAbout {
  background-color: #171617;
  display: flex;
  height: 100%;
}
.cointenerTextAbout {
    text-align: center;
    margin: auto;
    display: inline-block;
    display: grid;
    grid-template-columns: repeat(1, 60%);
    justify-content: center;
    width: 80%;
}
.photoPau{
    border-radius: 50%;
    width: 30%;
    margin: 0 auto;
    padding: 40px 0 40px 0 ;
}
.titleAbout {
  font-family: "Lato";
  font-size: 30px;
  color: #b3b3b3;
  padding: 0 0 20px 0;
}
.aboutMe {
  color: #b3b3b3;
  font-family: "Lato";
  font-size: 16px;
  text-align: center;
  padding: 0 0 30px 0;
}
.buttonAboutContact{
    margin: 0 5px 80px 0;
    padding: 10px;
    background: #2e2e2e;
    color: #B3B3B3;
    width: 30%;
}

.buttonAboutContact:hover{
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(70%, transparent), color-stop(0%, #00FA77));
    background-image: linear-gradient(transparent 70%, #00FA77 0%);
    background-position: left 30%;
    -webkit-transition: all 0.75s linear;
    transition: all 0.75s linear;
}

.cointenerAllCards{
    display: inline-block;
    display: grid;
    grid-template-columns: repeat(2, 40%);
    grid-gap: 55px;
   justify-content: center; 
   margin: 0 0 80px 0;
}
.cointenerCard{
    background: black;
    width: 100%;
    height: auto;
    border-radius: 5px;
    padding: 10px 30px 10px 30px;
}
.imgCard{
    text-align: center;
    margin: 20px 0 10px 0;
}
.proyectName{
    font-size: 25px;
}
.infoProyect{
    font-family: "Lato";
    font-size: 12px;
    padding: 15px;
    color: #B3B3B3;
}
.descriptionProyect{
    margin: 0 0 10px 0;
}
.containerTagList{
    display: inline-block;
}
.technilogies{
    font-family: "Lato";
    font-style:bold;
    font-size: 18px;
    margin-bottom: 10px;
}
.tagList{
    color: #B3B3B3;
    padding: 2px 10px;
    margin: 4px;
    background: #2e2e2e;
    border-radius: 50px;
}
.cointenerButtons{
    text-align: center;
    margin: 25px 0 20px 0;
}
.buttonProyect{
    margin: 0 5px 0 0;
    padding: 10px;
    background: #171617;
    color: #B3B3B3;
    width: 48%;
}

.buttonProyect:hover{
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(70%, transparent), color-stop(0%, #00FA77));
    background-image: linear-gradient(transparent 70%, #00FA77 0%);
    background-position: left 30%;
    -webkit-transition: all 0.75s linear;
    transition: all 0.75s linear;
}
.cointenerWorks{
    background-color: #171617;
    /*background: #131313;*/
    display: flex;
}
.cointenerCardsProyects{
    width: 80%;
}
.titleWorks{
    text-align: center;
    margin: 0;
    /*width: 100%;*/
    font-family: "Lato";
    font-size: 30px;
  color:  #b3b3b3;
    padding: 0px;
    margin: 50px 0 60px 0;
}
.cointenerContact {
  background: #171617;
  display: flex;
  height: 100vh;
}
.cointenerInfo{
    text-align: center;
    margin: auto;
    width: 80%;
}
.titleContact {
  font-family: "Lato";
  font-size: 30px;
  padding: 0px;
  color: #b3b3b3;
}
.spanContact{
    color: #00fa77;
}
.email{
    font-family: "Lato";
    font-size: 20px;
    color: #00fa77;
}
.cointenerButtonsContact {
    padding:80px 0 0 0;
    width: 100%;
    margin: auto;
  }
.buttonContact {
  background: black;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin: 20px;
}
i {
  color: #b3b3b3;
}
.buttonContact:hover{
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(70%, transparent), color-stop(0%, #00FA77));
    background-image: linear-gradient(transparent 70%, #00FA77 0%);
    background-position: left 30%;
    -webkit-transition: all 0.75s round;
    transition: all 0.75s round;
}

